"use client";

import type { RefObject } from 'react';
import { Image as ImageIcon, Star, Zap, Heart, Cloud, Music, Sparkles, Smile, Download } from 'lucide-react';
import { toPng } from 'html-to-image';
import { cn } from '@/lib/utils';

type PostTemplateProps = {
  title: string;
  subtitle: string;
  color: 'orange' | 'blue' | 'purple' | 'green';
  image: string | null;
  imageFit?: 'cover' | 'contain';
  imagePosition?: { x: number; y: number }; // 0-100 for percentage
  imageZoom?: number;
  reference: RefObject<HTMLDivElement>;
};

// Renk Temalarƒ± (√áok daha zengin gradientler)
const themes = {
  orange: {
    bg: "from-[#FF9A9E] via-[#FECFEF] to-[#FECFEF]", // Pastel Sunset
    text: "text-[#FF6B6B]",
    pill: "bg-[#FF6B6B]",
    icon: Star,
    iconColor: "text-yellow-400",
  },
  blue: {
    bg: "from-[#4FACFE] via-[#00f2fe] to-[#4FACFE]", // Malibu Beach
    text: "text-[#0093E9]",
    pill: "bg-[#0093E9]",
    icon: Cloud,
    iconColor: "text-white",
  },
  purple: {
    bg: "from-[#a18cd1] via-[#fbc2eb] to-[#a18cd1]", // Lavender
    text: "text-[#8E2DE2]",
    pill: "bg-[#8E2DE2]",
    icon: Heart,
    iconColor: "text-pink-400",
  },
  green: {
    bg: "from-[#84fab0] via-[#8fd3f4] to-[#84fab0]", // Sunny Morning
    text: "text-[#0ba360]",
    pill: "bg-[#0ba360]",
    icon: Zap,
    iconColor: "text-yellow-300",
  },
};

export function PostTemplate({
  reference,
  title,
  subtitle,
  color,
  image,
  imageFit = 'cover',
  imagePosition = { x: 50, y: 50 },
  imageZoom = 1,
}: PostTemplateProps) {
  const currentTheme = themes[color];
  const Icon = currentTheme.icon;

  // ƒ∞ndirme Fonksiyonu
  const handleDownload = async () => {
    if (reference.current) {
      try {
        const dataUrl = await toPng(reference.current, {
          pixelRatio: 2, // Daha y√ºksek √ß√∂z√ºn√ºrl√ºk i√ßin
          filter: (node: any) => !node.classList?.contains('exclude-from-export'), // Butonu g√∂rselden hari√ß tut
        });
        const link = document.createElement('a');
        link.download = `${title.replace(/\s+/g, '-').toLowerCase()}-post.png`;
        link.href = dataUrl;
        link.click();
      } catch (err) {
        console.error('Post indirme hatasƒ±:', err);
      }
    }
  };

  return (
    <div ref={reference} className="w-[1080px] h-[1350px] flex-shrink-0 relative overflow-hidden font-headline selection:bg-none group">
      
      {/* 1. KATMAN: Arka Plan + Noise Texture */}
      <div className={cn("absolute inset-0 bg-gradient-to-br flex flex-col items-center pt-20", currentTheme.bg)}>
        
        {/* Noise (Kumlanma) Efekti - Profesyonel doku katar */}
        <div className="absolute inset-0 opacity-[0.06] pointer-events-none mix-blend-overlay" 
             style={{ backgroundImage: `url("data:image/svg+xml,%3Csvg viewBox='0 0 200 200' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.65' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E")` }}>
        </div>

        {/* Arka Plan ≈ûekilleri (Soft Blobs) */}
        <div className="absolute top-[-10%] left-[-20%] w-[800px] h-[800px] bg-white/40 rounded-full blur-[120px] mix-blend-overlay"></div>
        <div className="absolute bottom-[-10%] right-[-20%] w-[600px] h-[600px] bg-white/30 rounded-full blur-[100px] mix-blend-overlay"></div>

        {/* 2. KATMAN: 3D Sticker ƒ∞konlar */}
        <div className="absolute top-32 left-10 transform -rotate-12 animate-bounce duration-[3000ms]">
           <Icon size={140} className={cn("drop-shadow-2xl filter", currentTheme.iconColor)} strokeWidth={2.5} fill="currentColor" />
        </div>
        <div className="absolute top-[500px] right-12 transform rotate-12">
           <Sparkles size={100} className="text-white drop-shadow-lg opacity-80" fill="white" />
        </div>
        <div className="absolute bottom-40 left-16 transform -rotate-45">
           <Smile size={110} className="text-white/80 drop-shadow-xl" strokeWidth={2.5} />
        </div>

        {/* 3. KATMAN: Yazƒ±lar (Titan One Font) */}
        <div className="text-center relative z-20 px-8 mb-8 mt-4">
          <h1 className={cn("text-[6.5rem] leading-[1] mb-6 drop-shadow-xl stroke-white", currentTheme.text)} 
              style={{ WebkitTextStroke: "4px white", textShadow: "6px 6px 0px rgba(0,0,0,0.1)" }}>
            {title}
          </h1>
          
          <div className={cn("inline-block px-10 py-4 rounded-full shadow-xl transform -rotate-2 border-4 border-white", currentTheme.pill)}>
             <p className="font-body font-black text-4xl text-white tracking-wide uppercase">
               {subtitle}
             </p>
          </div>
        </div>

        {/* 4. KATMAN: ULTRA GER√áEK√áƒ∞ TELEFON */}
        <div className="relative z-30 transform scale-[1.05] origin-top mt-8">
           
           {/* Telefon G√∂lgesi (Zemine d√º≈üen) */}
           <div className="absolute top-[50px] left-[50px] w-[580px] h-[1000px] bg-black/40 blur-[50px] rounded-[60px] transform rotate-[-1deg]"></div>

           {/* Telefonun Kendisi */}
           <div className="relative w-[600px] h-[1150px] bg-gray-900 rounded-[68px] p-[4px] shadow-2xl rotate-[-1deg]">
              
              {/* Metalik √áer√ßeve (Bezel Gradient) */}
              <div className="absolute inset-0 bg-gradient-to-tr from-gray-400 via-gray-100 to-gray-400 rounded-[64px]"></div>
              
              {/* ƒ∞√ß Siyah √áer√ßeve */}
              <div className="absolute inset-[4px] bg-black rounded-[60px]"></div>

              {/* EKRAN */}
              <div className="absolute inset-[16px] bg-black rounded-[48px] overflow-hidden border-[4px] border-black relative">
                {image ? (
                  <img
                    src={image}
                    className="w-full h-full"
                    alt="App Screen"
                    style={{ 
                      objectFit: imageFit, 
                      objectPosition: `${imagePosition.x}% ${imagePosition.y}%`,
                      transform: `scale(${imageZoom})`
                    }}
                  />
                ) : (
                  <div className="w-full h-full flex flex-col items-center justify-center bg-gray-800">
                     <ImageIcon size={80} className="text-gray-600 mb-4" />
                     <span className="text-gray-500 font-bold text-2xl font-body">G√∂rsel Y√ºkle</span>
                  </div>
                )}
                
                {/* Dynamic Island */}
                <div className="absolute top-6 left-1/2 transform -translate-x-1/2 w-[160px] h-[45px] bg-black rounded-full z-40 flex justify-end items-center pr-4">
                    <div className="w-3 h-3 rounded-full bg-[#1c1c1e] border border-[#2c2c2e]"></div>
                </div>

                {/* Cam Yansƒ±masƒ± (Glass Reflection) - √áok √∂nemli! */}
                <div className="absolute inset-0 bg-gradient-to-tr from-white/20 via-transparent to-transparent opacity-40 pointer-events-none rounded-[48px] z-50"></div>
              </div>
           </div>
        </div>

        {/* Alt Logo */}
        <div className="absolute bottom-12 z-40 bg-white/90 backdrop-blur-md px-6 py-3 rounded-2xl shadow-lg border border-white/50 flex items-center gap-3">
           <span className="text-3xl">ü¶ä</span>
           <span className={cn("font-headline text-3xl", currentTheme.text)}>KidsAI</span>
        </div>

        {/* ƒ∞ndirme Butonu (Export sƒ±rasƒ±nda gizlenir) */}
        <button
          onClick={handleDownload}
          className="exclude-from-export absolute top-8 right-8 z-50 bg-black/20 hover:bg-black/40 text-white p-4 rounded-full backdrop-blur-md transition-all opacity-0 group-hover:opacity-100 translate-y-[-10px] group-hover:translate-y-0 duration-300 shadow-2xl border border-white/20"
          title="Bu Postu ƒ∞ndir"
        >
          <Download size={32} />
        </button>

      </div>
    </div>
  );
}